import React from 'react';
import { graphql } from 'gatsby';
import Layout from '../components/layout';
import Subtitle from '../components/subtitle';
import SEO from '../components/seo';
import { Pane } from 'evergreen-ui';
import WorkCard from '../components/workCard';

const WorkPage = ({ data, location }) => {
  const siteTitle = data.site.siteMetadata.title;
  const workPosts = data.allMarkdownRemark.edges;

  return (
    <Layout location={location} title={siteTitle}>
      <SEO title="Work" />
      <Subtitle
        title="WORK"
        sub="Find few of my publicly available work here üî®"
      />
      <Pane
        display="flex"
        flexWrap="wrap"
        justifyContent="space-around"
        paddingBottom="16px"
        paddingLeft="16px"
        paddingRight="16px"
      >
        {workPosts.map((work) => {
          const slug = work.node.frontmatter.slug;
          const title = work.node.frontmatter.title || work.node.fields.slug;
          const desc = work.node.frontmatter.description;
          const demo = work.node.frontmatter.demo;
          const github = work.node.frontmatter.github;
          const node = work.node;
          return (
            <WorkCard
              desc={desc}
              title={title}
              github={github}
              story={`work/${slug}`}
              demo={demo}
            ></WorkCard>
          );
        })}
      </Pane>
    </Layout>
  );
};

export default WorkPage;

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(filter: { frontmatter: { template: { eq: "work" } } }) {
      edges {
        node {
          frontmatter {
            template
            title
            tags
            description
            slug
            category
            demo
            github
          }
          fields {
            slug
          }
        }
      }
    }
  }
`;
